'use client';
import { useEffect } from 'react';

type AccessibleOverlayProps = {
  onClose: () => void;
  ariaLabel?: string;
  className?: string;
  animate?: boolean;
};

export default function AccessibleOverlay({
  onClose,
  ariaLabel = 'Cerrar superposición',
  className = '',
  animate = false,
}: AccessibleOverlayProps) {
  // Cierre por tecla Esc
  useEffect(() => {
    const handleKey = (e: KeyboardEvent) => {
      if (e.key === 'Escape') onClose();
    };
    window.addEventListener('keydown', handleKey);
    return () => window.removeEventListener('keydown', handleKey);
  }, [onClose]);

  return (
    <div
      role="button"
      tabIndex={0}
      aria-label={ariaLabel}
      className={`fixed inset-0 bg-black/50 z-40 ${animate ? 'transition-opacity duration-300 opacity-100' : ''} ${className}`}
      onClick={onClose}
      onKeyDown={(e) => {
        if (e.key === 'Enter' || e.key === ' ') onClose();
      }}
    />
  );
}

