"use client";
import { useState, useEffect, useCallback } from "react";

type AlertaProps = {
  tipo: "error" | "success" | "info" | "confirmacion";
  mensaje: string;
  onClose: (resultado?: boolean) => void; // Para confirmación devuelve true/false
};

export default function Alerta({ tipo, mensaje, onClose }: AlertaProps) {
  const [visible, setVisible] = useState(true);

  // Función para cerrar la alerta
  const cerrar = useCallback(
    (resultado?: boolean) => {
      setVisible(false);
      setTimeout(() => onClose(resultado), 300);
    },
    [onClose]
  );

  // Autocierre para alertas que no sean error o confirmación
  useEffect(() => {
    if (tipo !== "error" && tipo !== "confirmacion") {
      const timer = setTimeout(() => cerrar(), 2500);
      return () => clearTimeout(timer);
    }
  }, [tipo, cerrar]);

  return (
    <div className={`alerta ${tipo} ${visible ? "visible" : ""}`}>
      <div className="alerta-mensaje">{mensaje}</div>

      {tipo !== "confirmacion" && tipo !== "error" && (
        <button className="cerrar-alerta" onClick={() => cerrar()} aria-label="Cerrar">
          &times;
        </button>
      )}

      {tipo === "confirmacion" && (
        <div className="alerta-botones-confirmacion">
          <button className="alerta-ok" onClick={() => cerrar(true)}>
            ACEPTAR
          </button>
          <button className="alerta-cancel" onClick={() => cerrar(false)}>
            CANCELAR
          </button>
        </div>
      )}
    </div>
  );
}
